import { AfterViewInit, Component, ElementRef, Input } from '@angular/core';

@Component({
  selector: 'bc-shortcut',
  templateUrl: './bc-shortcut.component.html',
})
export class BcShortcutComponent implements AfterViewInit {
  /**
   * Este valor de entrada define el tipo de shortcut.
   * Los valores permitidos [ single(default) | multiple | horizontal ]
   *
   * @type {string}
   * @memberof BcShortcutComponent
   */
  @Input() typeShortcut: 'single' | 'multiple' | 'horizontal' = 'single';

  /**
   * Nombre de la acción que describe el shortcut.
   *
   * @type {string}
   * @memberof BcShortcutComponent
   */
  @Input() text: string = 'Nombre de la acción';

  /**
   * Nombre del icono que identifica al shortcut.
   *
   * @type {string}
   * @memberof BcShortcutComponent
   */
  @Input() icon: string = 'help';

  /**
   * Descripción de la acción del shortcut en caso de que aplique.
   *
   * @type {string}
   * @memberof BcShortcutComponent
   */
  @Input() description: string;

  /**
   * Ruta hacia donde dirije el shortcut.
   *
   * @type {string}
   * @memberof BcShortcutComponent
   */
  @Input() route: string;

  /**
   * Permite indicar si el shortcut es nuevo.
   *
   * @type {boolean}
   * @memberof BcShortcutComponent
   */
  @Input() isNew: boolean = false;

  /**
   * Permite indicar si está deshabilitado
   *
   * @type {boolean}
   * @memberof BcShortcutComponent
   */
  @Input() isDisabled: boolean = false;

  constructor(private elRef: ElementRef) {}

  ngAfterViewInit(): void {
    this.overrideLinkClickEvent();
  }

  public overrideLinkClickEvent() {
    const link: HTMLElement = this.elRef.nativeElement.querySelector('a');
    if (!this.route) {
      link.addEventListener('click', (event: Event) => event.preventDefault());
    }
  }
}
